import {
	getUserAuth,
	auth2Page
} from '../../../utils/login.js'
import {
	showPic,
	navTo
} from '../../../utils/default.js'
export default {
	data() {
		return {
			userAuth: false,
			userInfo: null,
		}
	},
	methods: {
		showPic,
		navTo,
		getUserAuth(e) {
			if (getUserAuth.call(this, e)) {
				this.userAuth = true;
				this.userInfo = getApp().globalData.userInfo;
			}
		},
	},
	onLoad() {
		console.log('Mine onLoad');
		this.$state.setWatch(this, 'userAuth', 'userAuth');
		this.userInfo = getApp().globalData.userInfo;
		getApp().loginCB = () => {
			console.log('loginCB');
			this.userInfo = getApp().globalData.userInfo;
		}
	},
	onShow() {

	}
}
